nav {
    background: transparent;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 25%;
    transform:translateX(100);
    top: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 2rem;
    backdrop-filter: blur(2px);
}
nav a {
    background: transparent;
    padding: 1rem;
    border-radius: 10%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}
nav a:hover {
    background: rgba(0, 0, 0, 0.3);
}
nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}